<template>
  <div>
    <el-table
      :data="cateList"
      style="width: 100%; margin-bottom: 20px"
      row-key="id"
      border
      default-expand-all:false
      :tree-props="{ children: 'children' }"
    >
      <el-table-column prop="id" label="分类编号" width="120">
      </el-table-column>
      <el-table-column prop="catename" label="分类名称"> </el-table-column>
      <el-table-column prop="img" label="图片">
        <template slot-scope="scope">
          <img v-if="scope.row.img"
            :src="$img + scope.row.img"
            alt="无图片"
            style="width: 100px; height: 100px"
            onerror="../../assets/img/imgerro.jpg"
          />
        </template>
      </el-table-column>
      <el-table-column prop="status" label="状态">
        <template slot-scope="scope">
          <el-button type="primary" v-if="scope.row.status == 1" @click="scope.row.status = 2">启用</el-button>
          <el-button type="danger" v-else @click="scope.row.status = 1">禁用</el-button>
        </template>
      </el-table-column>
      <el-table-column prop="id" label="操作">
        <template slot-scope="scope">
          <el-button type="primary" @click="edit(scope.row.id)">编辑</el-button>
          <el-button type="danger" @click="del(scope.row.id)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import { successAlert } from "../../util/alert";
import { delCate } from "../../util/request";
export default {
  data() {
    return {};
  },
  computed: {
    ...mapGetters({
      cateList: "cate/getCate",
    }),
  },
  methods: {
    ...mapActions({
      requestCate: "cate/cateActions",
    }),
    edit(id) {
      this.$emit("editDetail", id);
    },
    del(id) {
      this.$confirm("您确定要删除吗?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          delCate({ id: id }).then((res) => {
            if (res.data.code == 200) {
              successAlert(res.data.msg);
              this.requestCate();
            }
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除",
          });
        });
    },
  },
  mounted() {
    this.requestCate();
  },
};
</script>

<style>
</style>


