<template>
  <div>
    <el-dialog :title="info.title" :visible.sync="info.isShow">
      <el-form :model="form">
        <el-form-item label="上级分类" :label-width="formLabelWidth">
          <el-select v-model="form.pid" placeholder="请选择">
            <el-option label="顶级菜单" :value="0"></el-option>
            <el-option v-for="item in list" :key="item.id" :label="item.catename" :value="item.id"></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="分类名称" :label-width="formLabelWidth">
          <el-input v-model="form.catename" placeholder="请输入内容"></el-input>
        </el-form-item>

        <el-form-item label="图片" :label-width="formLabelWidth">
          <el-upload
            class="avatar-uploader"
            action="#"
            :show-file-list="false"
            :on-change="changeImg"
          >
            <img v-if="imageUrl" :src="imageUrl" class="avatar" />
            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
          </el-upload>
        </el-form-item>

        <el-form-item label="状态" :label-width="formLabelWidth">
          <el-switch
            v-model="form.status"
            active-color="#409eff"
            inactive-color="grey"
            :active-value="1"
            :inactive-value="2"
          >
          </el-switch>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancel">取 消</el-button>
        <el-button type="primary" v-if="info.isAdd" @click="ok">添 加</el-button>
        <el-button type="primary" v-else @click="edit">修 改</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import {successAlert, warningAlert} from "../../util/alert"
import {addCate,cateDetail,cateEdit} from "../../util/request"
import {mapGetters,mapActions} from "vuex"
export default {
  props: ["info"],
  data() {
    return {
      form: {
        pid: 0,
        catename: "",
        img: "",
        status: 1,
      },
      formLabelWidth: "120px",
      imageUrl: ''
    };
  },
  computed:{
      ...mapGetters({
          list:"cate/getCate"
      })
  },
  methods: {
      ...mapActions({
          requestCate:"cate/cateActions"
      }),
       changeImg(file) {
           //处理文件大小
           if(file.size>2*1024*1024){
               warningAlert("上传图片不能超过2M")
               return
           }
           //处理文件格式
           var ex=[".jpg",".png",".gif"]
           
           var exname=file.name.slice(file.name.lastIndexOf("."))
           if(!ex.some(item=>item==exname)){
               warningAlert('上传文件格式不正确')
               return
           }
           //生成线上显示的图片
        this.imageUrl = URL.createObjectURL(file.raw);
        //将图片信息保存到form
        this.form.img=file.raw
      },
    cancel() {
      this.info.isShow = false;
      this.form = {
        pid: 0,
        catename: "",
        img: "",
        status: 1,
      };
    },
    detailC(id){
        cateDetail({id:id}).then(res=>{
            this.form=res.data.list
            this.form.id=id
            this.imageUrl=this.$img+this.form.img
        })
    },
    edit() {
        cateEdit(this.form).then(res=>{
           if(res.data.code==200){
                successAlert(res.data.msg)
                this.cancel();
                this.requestCate()
           }else{
               warningAlert(res.data.msg)
           }
        })
    },
    ok() {
        addCate(this.form).then(res=>{
            if(res.data.code==200){
                successAlert(res.data.msg);
                this.cancel()
                this.requestCate()
            }else{
                warningAlert(res.data.msg)
            }
        })
    },
  },
};
</script>

<style scoped>
.avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
 .el-icon-plus{
       border: 1px dashed #d9d9d9;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }

</style>


